import React, { useEffect } from "react";
import {
  CircularProgressbarWithChildren,
  buildStyles,
} from "react-circular-progressbar";
import ProgressBar from "react-bootstrap/ProgressBar";

import ListItem from "@mui/material/ListItem";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import Avatar from "@mui/material/Avatar";
import "aos/dist/aos.css";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";

import AOS from "aos";
import "aos/dist/aos.css";

import ModalImage from "react-modal-image";
import Typography from "@mui/material/Typography";

import { useParams } from "react-router-dom";

import { getSkill, clearSkill } from "../redux/features/skillSlice";
import { easeQuadInOut } from "d3-ease";
import AnimatedProgressProvider from "./AnimatedProgressProvider";
import "animate.css/animate.min.css";

import { useSelector, useDispatch } from "react-redux";

import Divider from "@mui/material/Divider";
import "react-quill/dist/quill.snow.css";

const SkillCardDetail = () => {
  const { Skill } = useSelector((state) => ({ ...state.skill }));
  const { id } = useParams();

  useEffect(() => {
    if (id) {
      dispatch(getSkill(id));
    }
    dispatch(clearSkill());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id]);
  const progress = {
    width: "100%",
  };
  AOS.init();
  const Item = styled(Paper)(({ theme }) => ({
    background: "rgba(255, 255, 255, 0.2)",
    borderRadius: "16px",
    boxShadow:
      "rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px",

    WebkitBackdropFilter: "blur(5px)",
    border: "1px solid rgba(255, 255, 255, 0.3)",
  }));

  const dispatch = useDispatch();

  return (
    <div style={{ marginTop: "60px" }}>
      <>
        <style type="text/css">
          {`   

.progress-bar{  background-color:  rgba(78, 78, 246, 0.647); 
  font-size:17px;
color:black; }
.progress{
  background-color: rgba(247, 90, 216, 0.696);
}


}

`}
        </style>
      </>
      <br></br>
      <br></br>
      <Grid
        container
        spacing={1}
        style={{ display: "flex", justifyContent: "center" }}
      >
        <br></br>
        <br></br>
        <br></br>

        <Grid item xs={11}>
          <Item className="moreproject">
            <br></br>
            <div
              className="About3"
              data-aos="zoom-in"
              data-aos-delay="200"
              data-aos-duration="500"
            >
              {" "}
              <h1> {Skill.title}</h1>
            </div>
            <br></br>
            <div
              style={{
                width: "100%",
                height: 300,
                textAlign: "center",
                alignContent: "center",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <AnimatedProgressProvider
                valueStart={1}
                valueEnd={Skill.percentage}
                duration={1.4}
                easingFunction={easeQuadInOut}
              >
                {(percentage) => {
                  const roundedpercentage = Math.round(percentage);
                  return (
                    <CircularProgressbarWithChildren
                      value={roundedpercentage}
                      styles={buildStyles({
                        textColor: "black",
                        pathColor: "rgba(78, 78, 246, 0.647)",
                        border: "1px solid black",
                        trailColor: "rgba(247, 90, 216, 0.696)",
                        pathTransition: "none",
                      })}
                    >
                      <img
                        style={{
                          width: "150px",
                          height: "150px",

                          borderRadius: "50%",
                        }}
                        src={Skill.imageFile}
                        alt="sufi"
                      />
                      <div style={{ fontSize: 12, marginTop: 5 }}>
                        <Typography sx={{ fontSize: "26px", color: "blue" }}>
                          {`${roundedpercentage}%`}
                        </Typography>
                      </div>
                    </CircularProgressbarWithChildren>
                  );
                }}
              </AnimatedProgressProvider>
            </div>
            <br></br>
            <ListItem
              style={{
                width: "90%",
                position: "relative",
                left: "5%",

                textAlign: "center",
                alignContent: "center",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <ListItemAvatar>
                <Avatar
                  style={{
                    height: "60px",
                    width: "60px",
                    background: "none",
                  }}
                >
                  <img
                    className="i-swing"
                    src={Skill.imageFile}
                    alt=""
                    style={{ width: "100%", height: "auto" }}
                  />
                </Avatar>
              </ListItemAvatar>
              <ProgressBar
                animated
                now={Skill.percentage}
                style={progress}
                label={`${Skill.percentage}%`}
              />
            </ListItem>
            <br></br>
            <h2
              style={{ fontSize: "38px" }}
              data-aos="zoom-in"
              data-aos-delay="400"
              data-aos-duration="500"
            >
              {" "}
              About {Skill.title}
            </h2>{" "}
            <div
              className="QuillDescription"
              data-aos="zoom-in"
              data-aos-delay="500"
              data-aos-duration="500"
            >
              <p
                style={{
                  fontSize: "18px",
                }}
                data-aos="zoom-in"
                data-aos-duration="800"
              >
                <div className="QuillDescription PrjectsDescription">
                  <p dangerouslySetInnerHTML={{ __html: Skill.description }} />
                </div>
              </p>
            </div>
            <br></br>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                overflow: "hidden",
              }}
            >
              <div className="projectImg">
                <div>
                  <ModalImage
                    small={Skill.imageFile1}
                    large={Skill.imageFile1}
                    alt="Hello World!"
                    className="modal-image"
                  />
                </div>
              </div>
            </div>
            <br></br>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                overflow: "hidden",
              }}
            >
              <div className="projectImg">
                <div>
                  <ModalImage
                    small={Skill.imageFile2}
                    large={Skill.imageFile2}
                    alt="Hello World!"
                    className="modal-image"
                  />
                </div>
              </div>
            </div>
            <Divider />
            <br></br>
          </Item>
        </Grid>
      </Grid>{" "}
      <br></br>
      <Divider style={{ width: "100%" }} />
      <br></br>
    </div>
  );
};

export default React.memo(SkillCardDetail);
